import React from "react";
import FeaturedTable from "./Editable";
import PropTypes from "prop-types";
import "./styles.css";

const Table = React.forwardRef((props, ref) => (
  <FeaturedTable {...props} ref={ref} />
));

Table.defaultProps = {
  editable: false,
  editTools: ["edit", "add"],
  editToolsConfig: {
    position: "bottom",
    itemStyle: { marginLeft: "5px" },
    editText: "",
    editIcon: "",
    addText: "",
    addIcon: "",
    deleteText: "",
    deleteIcon: "",
    okText: "",
    okIcon: "",
    cancelText: "",
    cancelIcon: ""
  },
  defaultAddCount: 1,
  isAppend: false,
  validateTrigger: "onSave",
  validateDelay: 300,
  allowSaveEmpty: false,
  dataControled: false,
  alwaysValidate: false,
  settable: true,
  pagination: false
};

Table.propTypes = {
  /**  */
  editable: PropTypes.bool,
  /**  */
  pagination: PropTypes.oneOfType([PropTypes.bool, PropTypes.object]),
  /**  ['edit', 'add','delete',{icon:"",text:"",props:{},handler:Function},Function] addSingle: */
  editTools: PropTypes.array,
  /** { position: "bottom", itemStyle: { marginLeft: "5px" }, editText: "", editIcon: "", addText: "", addIcon: "", deleteText: "", deleteIcon: "", okText: "", okIcon: "", cancelText: "", cancelIcon: "" } */
  editToolsConfig: PropTypes.object,
  /**  */
  isAppend: PropTypes.bool,
  /**  */
  defaultAddCount: PropTypes.number,

  /**  (changedRows,newRows,editType)=>void
   * @param {Array} changedRows-
   * @param {Array} newRows-
   * @param {string} editType-;"edit":;"add":;"delete":
   */
  onEditSave: PropTypes.func,
  /** onEditSave */
  allowSaveEmpty: PropTypes.bool,
  /**  */
  validateTrigger: PropTypes.oneOf(["onChange", "onBlur", "onSave"]),

  /** false ()=>bool */
  onBeforeAdd: PropTypes.func,

  /**  (addedData, newData)=>void
   * @param {Array} addedData-
   * @param {Array} newRows-
   */
  onAdd: PropTypes.func,

  /**  (rowIndex)=>object
   * @param {numer} rowIndex
   * @returns {object} 
   */
  rowTemplate: PropTypes.func,

  /**  */
  onCancel: PropTypes.func,
  /** false */
  onBeforeEdit: PropTypes.func,

  /**  */
  onEdit: PropTypes.func,
  /**
   * ,false 
   */
  onBeforeDelete: PropTypes.func,
  /**  */
  onDelete: PropTypes.func,
  /**  */
  alwaysValidate: PropTypes.bool,
  /** onEditSaveonCancel */
  dataControled: PropTypes.bool,
  /**  */
  settable: PropTypes.bool,
  /** ididlocalStorageid */
  tableId: function(props, propName, componentName) {
    let count = 0;
    let v = props[propName];

    if (typeof v !== "undefined" && v !== "") {
      let tbs = document.getElementsByClassName("table-extend");

      for (let i = 0, len = tbs.length; i < len; i++) {
        const tb = tbs[i];
        if (tb) {
          const t = tb.getAttribute("data-tableid");
          if (t === v) {
            count = count + 1;

            if (count > 1) {
              break;
            }
          }
        }
      }
    }

    if (count > 1) {
      return new Error(
        ` Encountered two table with the same tableId, '${v}'.The tableId must be unique in the whole application.
                  We Recommended set the tableId based on file path.
                  eg: platform/user/index.js =>  platform-user-xxx `
      );
    }
  }
};

export default Table;
